    textCopy: ' <div class="grid-cards"><h3 class="page-title">Electrical and Computer Engineering Partnerships (ECEP)</h3><p style="text-indent:0"><span class="first-letter">The Electrical</span>and Computer Engineering Department has been forging new partnerships with industry over the past year with its Electrical and Computer Engineering Partnerships (ECEP) program. The program tightly couples academic education and research with the needs of industry by working directly with partners in nurturing our pipelines of talent both at the undergraduate and graduate levels. We enhance visibility to our student activities with industry through open houses and research reviews, and we propagate industry needs with our students through hands-on projects, material in courses, industry lecturers, and collaborative research opportunities. ECEP also provides linkages for partners to access UCLAs state-of-the-art facilities. These activities are possible through the support of our partners. More details are available at the ECEP website:<span><a class="link" target="_blank" rel="noopener" href="https://www.ee.ucla.edu/industry">https://www.ee.ucla.edu/industry.</a></span></p><div class="card-deck"><div class="card-partner"><img class="logo-pic" src="/img/aerospace_corporation_logo.svg" alt="ECE Industry partner "></div><div class="card-partner"><img class="logo-pic" src="/img/Broadcom.svg" alt="ECE Industry partner "></div><div class="card-partner"><img class="logo-pic" src="/img/Mediatek.png" alt="ECE Industry partner "></div><div class="card-partner"><img class="logo-pic" src="/img/Northrop_Grumman_logo.svg" alt="ECE Industry partner "></div><div class="card-partner"><img class="logo-pic" src="/img/qualcomm-logo.svg" alt="ECE Industry partner "></div><div class="card-partner"><img class="logo-pic" src="/img/Raytheon_logo.svg" alt="ECE Industry partner "></div></div><div class="spacer"><img src="/img/pixel.png"></div></div> '
