    textCopy: '<p style="text-indent:0"><span class="first-letter">S</span>ergio Carbajo received the Young Investigator Program Awards from the Office of Naval Research (ONR) 2024 as well as the Young Investigator Research Program from the Air Force Office of Scientific Research (AFOSR) in recognition of his work in laser and optical physics.  The grants will support Carbajos research on innovative quantum electrodynamics to enable new regimes in ultracompact X-ray lasers. </p><p> Carbajo leads the Quantum Light-Matter Cooperative (QLMC) at UCLA, which seeks to explore and advocate for the advancement of photo sciences and light-matter interactions across a broad range of applications. A member of the California NanoSystems Institute at UCLA, Carbajo holds a joint faculty appointment in the Physics and Astronomy Department. He is also a visiting professor at Stanford Universitys Photon Science Division at SLAC National Accelerator Laboratory. In addition to his research, Carbajo is interested in promoting equity, diversity and inclusion (EDI) in the scientific fields, serving as the EDI officer of the Electrical and Computer Engineering Department and is the founding director of the Queered Science and Technology Center.</p><p>   Among the accolades Carbajo has received for his work in photon sciences are the 2021 Horizon Prize from the Royal Society of Chemistry, the 2021 SPIE Early Career Award and the 2019 Japan Society for the Promotion of Science Fellowship. In 2023 he was elected a Senior Member of the Institute of Electrical and Electronics Engineers (IEEE). </p><p>  Established in 2008, the NRO award support early-career faculty members who show exceptional promise and creativity in their research. Carbajo  will receive a three-year, $750,000 grant to support his research, joining 22 other recipients honored in the 2024 award cohort. As an executive branch agency within the Department of Defense, the Office of Naval Research provides technical advice to the chief of naval operations and the secretary of the Navy. Recent UCLA Samueli recipients of the Young Investigator Awards include Xiang Anthony Chen, and Kunihiko Sam Taira, a professor of mechanical and aerospace engineering.</p><p>  The Air Force Office of Scientific Research grant is worth $450,000 for three years. the AFOSR is the basic research arm of the Air Force Research Laboratory. The Young Investigator Research Program awarded a total of $25 million in 2023 to 58 investigators from 44 research institutions in the U.S</p> '
