    articleCaption2: '',
    articleCaption3: '',
    textCopy: '<p style="text-indent:0"><span class="first-letter">D</span>istinguished Professor Yahya Rahmat-Samii received the prestigious IEEE Antennas and Propagation Society (AP-S) Harrington-Mittra Award in Computational Electromagnetics. As stated by 2022 IEEE Antenna and propagation Society President, Prof. G. Lazzi, shown in the photo below, As the 2022 IEEE AP-S President, it is my distinct pleasure and honor to inform you that the IEEE Antennas and Propagation Society has decided to recognize your fundamental contributions with the Harrington-Mittra Award in Computational Electromagnetics.</p> <p>The Harrington-Mittra Award in Computational Electromagnetics is given annually to recognize outstanding achievement in Computational Electromagnetics. The criteria considered by the AP-S Awards Committee include: (a) Fundamental contributions to theory and applications of integral equations, differential equations and asymptotic methods in electromagnetics (b) Innovative numerical methods for Antennas and EM Scattering problems and (c) Innovative numerical methods for analysis of propagation of EM waves.</p> <p> Professor Rahmat-Samii was honored for his pioneering contributions in the areas of advanced computational techniques in applied electromagnetics and antennas. The award plaque, shown in the photo, reads, For fundamental contributions to the spectral theory of diffraction; hybrid method of moments and physical optics; and nature-inspired optimization techniques in diverse electromagnetics engineering applications.. His original computational and optimization concepts and techniques, appearing in many of his papers and several of his books, have been used in the design of many spacecraft antennas for planetary, remote sensing, radio astronomy and Cubesat missions, radar cross section characterizations including stealth and antenna design applications. Professor Rahmat-Samii received his award in Denver, at the 2022 IEEE International Symposium on Antennas and Propagation and USNC-URSI Radio Science Meeting Award Ceremony on Wednesday July 13, 2022. Both IEEE President and IEEE Antennas and Propagation President were in attendance. The IEEE Antennas and Propagation Society has four major field awards, namely, Distinguished Achievement Award, Chen-To Tai Distinguished Educator Award, John Kraus Antenna Award and Harrington-Mittra Award in Computational Electromagnetics and Prof. Rahmat-Samii is the only member of this society who has won all of them. He was also the recipient of the IEEE Electromagnetics Field Award in 2011.</p> <p></p> <p>    Distinguished Professor Yahya Rahmat-Samii received the 2023 USNC-URSI Outstanding Educator Award</p> <p> Professor Yahya Rahmat-Samii received the 2023 USNC-URSI Outstanding Educator Award at a radio science conference held in Boulder, Colorado, Jan. 10-13, 2023. This award has recently been established to be given once every three years and 2023 is the inaugural year of this award. The award is sponsored by the United States National Committee (USNC) for the International Union of Radio Science (URSI) under auspicious of the US National Academies of Sciences-Engineering-Medicine. Professor Rahmat-Samii was honored for his contributions as an outstanding educator. The award statue, shown in the photo, reads, USNC-URSI Outstanding Educator Award is presented to Yahya Rahmat-Samii: For outstanding contributions to development of inspiring methodologies in educating a vast number of students and engineers in modern electromagnetics and communication antennas. Professor Rahmat-Samii received his award at the University of Colorado, Boulder Colorado, at the 2023 National Radio Science Conference Award Ceremony on Thursday January 12, 2023. The conference is sponsored by the USNC-URSI and also co-sponsored by the IEEE Antennas and Propagation Society. The conference covers diverse topics of the ten commissions of URSI addressing all aspects of Radio Science. Prof. Rahmat-Samii served USNC-URSI as the elected President, 2009-2011, representing US radio scientists at the URSI General Assembly in 2011.</p> <p> Distinguished Prof. Rahmat-Samii was the Keynote Speaker at the 2022 IEEE CAMA Conference, Guangzhou, China</p><p>Distinguished Prof. Yahya Rahmat-Samii delivered the conference keynote speech titled, From Archimedes Burning Mirror to James Webb Space Telescope: A Journey from the Ancient Past to the Renaissance and the Present at the 2022 IEEE CAMA (Conference on Antenna Measurements & Applications) in Guangzhou, China, December 15, 2022. The conference was conducted online via zoom and attracted a large number of international participants. This conference was held in China for the first time covering all aspects of advanced antenna measurement and diagnostic techniques in addition to diversified antenna applications. Prof. Rahmat-Samii has pioneered many modern topics in reflector antenna designs including, antenna concepts for satellite communications, earth observations and remote sensing, CubeSats and SmallSats, radio astronomy and others. Many of his publications on these topics have appeared on the cover pages of various IEEE publications. He also teaches a course on these topics at UCLA.</p>'
