    articleCaption1: 'Before-and-after images using the Vision Enhancement via Virtual-diffraction and coherent Detection algorithm',
    articleCaption2: '',
    articleCaption3: '',
    textCopy: '  <p style="text-indent:0"><span class="first-letter">A</span> team of engineers at the UCLA Samueli School of Engineering has developed a powerful computational imaging algorithm that can self-correct pictures taken with poor lighting to produce high-resolution images in real time. The algorithm enhances the visibility and color of both still images and videos at record speed. Led by Bahram Jalali, distinguished professor emeritus of electrical and computer engineering as well as bioengineering, the team has established a new paradigm for algorithm design where laws of physics provide a blueprint for crafting algorithms.</p><p>  The team published this week its aptly named approach VEViD, which stands for Vision Enhancement via Virtual-diffraction and coherent Detection, in the optics journal, eLight. Callen MacPhee, a UCLA electrical engineering graduate student pursuing a masters degree, is the co-author. The new methodology is in sharp contrast to both classical algorithms based on empirical hand-crafted rules and modern artificial intelligence algorithms that rely on a massive amount of computations involving millions of parameters. Our new algorithm builds on more than two decades of research in optical physics in our laboratory and has direct applications in augmented reality, night-time driving, biology, and space exploration, Jalali said. Comparison of VEViD with leading neural network algorithms shows comparable or better image quality but with one to two orders of magnitude faster-processing speed.</p><p> The physics-inspired approach leads to a surprisingly simple and computationally efficient algorithm with exceptional image quality and processing speed. The algorithm can enhance 4K-resolution videos at more than 200 frames per second, much faster than other algorithms with similar image quality.</p><p> Under low-light conditions such as at night time, digital images suffer from low contrast and loss of information. VEViD is designed to improve these qualities for two purposes: increased visual quality for human perception and increased object-detection accuracy using machine learning. The technology has applications in security cameras, autonomous vehicles, robots, vision assistance for the elderly and live cell tracking for drug development.</p><p>  The team has successfully demonstrated VEViDs ability to perform a color enhancement and managed to pair the algorithm with a neural network trained to detect objects. VEViD can act as a pair of glasses for computer vision algorithms trained on daylight scenes  allowing them to see and detect objects in the dark, MacPhee said. This makes those networks more robust while also saving training time and energy.</p><p> The research was supported by grants from the Parker Center for Cancer Immunotherapy and the Office of Naval Research. The UCLA team will make its codes and data available on the publicly accessible GitHub repository.</p> '
