    textCopy: '    <p style="text-indent:0"><span class="first-letter">L</span>Assistant professor Yuan Tian of the UCLA Samueli School of Engineering has received an Okawa Foundation Research Grant, which is awarded to researchers whose work helps advance the development of information and communications technology. The one-year grant will help fund Tians research on developing security, privacy and safety measures for trustworthy human-artificial intelligence interactions. </p> <p>Tians research focuses on new technologies to protect user security and privacy for machine learning and Internet of Things. Her work has been featured in security conferences and adopted by major platform designers and app developers such as Chrome, Firefox and Android. With funding support from the Institute for Technology, Law & Policy, which is affiliated with both the UCLA School of Law and UCLA Samueli, Tian joined UCLA in July of 2022 from the University of Virginia where she was an assistant professor of computer science. </p> <p> The Okawa foundation is a Japanese public foundation established by information technology pioneer Isao Okawa. Over the last 35 years, the foundation has recognized and supported research efforts that will contribute to an advanced information society.  Tian is the principal investigator on four National Science Foundation research grants and has received a Google Research Scholar Award, a National Science Foundation CAREER Award, a Facebook Research Award and an Amazon AI Faculty Fellowship.  </p> '
