  textCopy: '  <p style="text-indent:0"><span class="first-letter">P</span>aulo Tabuada is a principal investigator of a multidisciplinary university research team tasked to revamp control systems that involve interactions between their physical and cyber components. Such systems consist of physical components that are constantly running and software modules that are executed periodically or sporadically, depending on specific events. Unfortunately, progress in hybrid control systems has been hampered by the difficulty to reconcile these two distinctly different types of components, said Tabuada, who holds the UCLA Vijay K. Dhir Chair in Engineering. To address this challenge, we will rebuild the theoretical foundations of hybrid control systems using a suite of advanced mathematical tools that are less known in engineering.</p><p>  Led by the University of Illinois Urbana-Champaign, the Hybrid Dynamics  Deconstruction and Aggregation project, or HyDDRA, is part of the U.S. Department of Defenses 2023 Multidisciplinary University Research Initiative Program. HyDDRA is one of 31 research teams from 61 academic institutions funded by the program, with an average award of $7.1 million over five years.</p><p>  The HyDDRA project also includes researchers from the  University of Pennsylvania, Caltech and the TOPOS Institute in Berkeley, California. Since its creation in 1985, the Department of Defense program has funded teams of investigators from multiple disciplines to facilitate the growth of cutting-edge technologies. The 2023 it awards totaled $220 millions.</p> '
