    articleCaption3: '',
    textCopy: '<p style="text-indent:0"><span class="first-letter">B</span>y the day, it is becoming more and more clear that emerging applications involving AI, extended reality, digital twins, and real-time control are shaping up to transform nearly all dimensions of daily life by the end of the decade, if not sooner. To fully deliver these transformative technologies, however, a new foundation of wireless connectivity is needed. </p><p>Wireless networks like cellular systems and Wi-Fi are typically evaluated on their ability to meet quantifiable requirements, most often in terms of network throughput, latency, and reliability. Future wireless networks will be expected to provide connectivity that is not only more stringent in terms of throughput, latency, and reliability but also more heterogeneous than ever before. Immersive experiences in extended reality, for example, will require extremely high throughput for rich visual scenes, extremely low latency for seamless interactions with others, and extremely high reliability for uninterrupted use and ubiquitous adoption. Interestingly, future wireless networks are also being tasked with providing new, expanded services beyond connectivity such as sensing and localization for applications aiming to perceive, digitize, and/or interact with the real world. </p><p>My goal is to conduct research and create technologies that drive the design, development, and deployment of these future wireless networks, with a particular focus on 5G and future 6G cellular networks. To accomplish this, my lab uses mathematical tools, machine learning, and real-world hardware to analyze, model, optimize, and prototype wireless systems and solutions. When possible and practical, we incorporate experimental components into our research efforts by collecting measurements, prototyping solutions, and evaluating proposed designs, which ultimately allows us to conduct research more likely to have real-world impact.</p><p>Beyond research, I am passionate about teaching students the fundamentals of wireless communication and introducing them to the technologies that have rapidly transformed wireless connectivity over the past two decades. My aim is to equip students with the skills and knowledge necessary to become wireless engineers capable of solving real-world problems and to excite them about the underlying technologies connecting our world.</p><p>Bio: Ian P. Roberts is an Assistant Professor of Electrical and Computer Engineering at UCLA, where he leads the Wireless Lab in conducting theoretical and experimental research on wireless communication and sensing. Prior to joining UCLA, he completed his Ph.D. at the University of Texas at Austin, where he was an NSF Graduate Research Fellow in the Wireless Networking and Communications Group. He has industry experience developing and prototyping wireless technologies at AT&T Labs, Amazon, GenXComm (startup), Sandia National Labs, and Dynetics (defense contractor). During his time at GenXComm, he created and demonstrated their earliest wireless solutions combining RF, photonic, and digital signal processing for interference cancellation. In 2023, he received the Andrea Goldsmith Young Scholars Award from the IEEE Communications Society for his work on the theory and practice of full-duplex millimeter-wave communication systems.  </p>'
