    textCopy: '   <div class="grid-layout"><div class="full"><h3 class="page-title">Design Automation for Computer Systems</h3><p></p><div class="page-text"><p style="text-indent:0"><span class="first-letter">We investigate</span> methodologies and algorithms for the design of complex systems, including circuits and semiconductor technologies, robots, cyberphysical and energy systems, and the Internet of Things.</p><p>The effects of nanoscale semiconductor technologies span the entire hardware-software stack, and we investigate how technology changes in devices as well as semiconductor fabrication influence design (especially layout). We study digital, mixed-signal, and FPGA-based configurable integrated circuits in the context of emerging challenges of energy efficiency, security, reliability, variability and manufacturability. We also explore architecture and system software techniques to mitigate variability and reliability challenges of increasingly unpredictable circuit fabric.</p><p>Robotics and cyberphysical systems is a rapidly growing field that spans a range of engineering disciplines. We study how such a broadly scoped area can be rigorously codified, inventing representations for the formal definition and analysis of interdisciplinary design. With an eye towards manufacturability, we investigate methods of encapsulating engineering principles and distilling them into design automation algorithms. Our research seeks to dramatically reduce cost, increase proliferation, and promote the democratization of design of hardware-software systems.</p></div></div><div class="full"><div class="prof-pics"><div><img class="fac" src="/img/Jason_Cong.jpg" alt="UCLA ECE Faculty Jason Cong"></div><div><img class="fac" src="/img/gupta.jpg" alt="UCLA ECE Faculty Puneet Gupta"></div><div><img class="fac" src="/img/Ankur_Mehta.jpg" alt="UCLA ECE Faculty CES Ankur Mehta"></div></div><div class="page-caption faculty-caption"><b>Faculty:</b> Jason Cong, Puneet Gupta, Ankur Mehta (pictured). Also: M.-C. Frank Chang, Lei He</div></div></div><div class="grid-layout"><div class="full"><h3 class="page-title">Energy-Efficient Digital Architectures and Circuits</h3><div class="page-text"><p style="text-indent:0"><span class="first-letter">We are focusing</span>on digital integrated circuit optimization in the power-area-performance space, its application to emerging health care and communication algorithms, and the investigation of design principles with post-CMOS devices. This includes advanced communication algorithms such as those found in future cognitive radios and new advances in biomedical applications.</p></div></div><div class="full"><div class="prof-pics"><div><img class="fac" src="/img/Cabric.jpg" alt="UCLA ECE Faculty DanijelaCabric"></div><div><img class="fac" src="/img/Subramanian_Iyer.jpg" alt="UCLA ECE Faculty Subramanian S. Iyer"></div><div><img class="fac" src="/img/markovic5.jpg" alt="UCLA ECE Faculty Dejan Markovic"></div></div><div class="page-caption"><b>Faculty:</b> Danijela Cabric, Subramanian S. Iyer, Dejan Markovic (pictured). Also: Jason Cong, Lei He, C.-K. Ken Yang.</div><div class="spacer"><img src="/img/pixel.png"></div><div class="prof-pics"><div><img class="fac" src="/img/Rajeev_Jain.jpg" alt="UCLA ECE Faculty Jain"></div><div><img class="fac" src="/img/Gabor_C_Temes.jpg" alt="UCLA ECE Faculty Temes"></div><div><img class="fac" src="/img/Alan-Willson.jpg" alt="UCLA ECE Faculty Allan Wilson"></div></div><div class="page-caption faculty-caption"><b>Emeriti Faculty:</b>Rajeev Jain, Gabor Temes, Alan Willson</div></div></div><div class="grid-layout"><div class="full"><h3 class="page-title">Neuroengineering</h3><div class="page-text"><p style="text-indent:0"><span class="first-letter">We address</span> challenges that limit our ability to obtain information as well as our ability to process it in incredibly small and low-power form factors, to advance technology for science, therapy and global health. Our work brings together low-power data processing, biosignal interfaces, communication, and energy aspects to push the limits of biosignal transducer systems, where the energy, size and processing requirements are often several orders of magnitude more challenging than in conventional applications. The cross-disciplinary nature of our work naturally stimulates collaboration across the areas of signal processing, circuits and systems, and devices, as well as interactions at the biological and engineering levels.</p></div></div><div class="full"><div class="prof-pics"><div><img class="fac" src="/img/Jason_Cong.jpg" alt="UCLA ECE Faculty Jason Cong"></div><div><img class="fac" src="/img/markovic5.jpg" alt="UCLA ECE Faculty Markovic"></div><div><img class="fac" src="/img/mayank-mehta.jpg" alt="UCLA ECE Faculty mayank mehta"></div></div><div class="page-caption faculty-caption"><b>Faculty:</b> Jason Cong, Dejan Markovic, Mayank Mehta</div></div></div><div class="grid-layout"><div class="full"><h3 class="page-title">Communications Circuits</h3><p></p><div class="page-text"><p style="text-indent:0"><span class="first-letter">We develop</span> integrated circuits for data communications spanning the entire gamut of data rates, ranges, and communication media: from wireless to wired, from PCB traces to plastic waveguides, from intra-chip to long haul links, from cellular to space communications, from VHF to terahertz frequencies, and from low power links to multi-Gb/s links. Our focus is on CMOS electronics and our faculty members are pioneers in this field. We address the fundamental challenges posed by noise, device nonlinearity, and variability in affecting communications in harsh environments, employing theoretical analysis, creative circuit design, and algorithmic digital correction techniques. The recent focus has been on enabling true software defined radios, mm-wave and terahertz applications.</p></div></div><div class="full"><div class="prof-pics"><div><img class="fac" src="/img/Asad_Abidi.jpg" alt="UCLA ECE Faculty Asad_Abidi"></div><div><img class="fac" src="/img/wentai_liu.jpg" alt="UCLA ECE Faculty wentai_liu"></div><div><img class="fac" src="/img/pamarti.jpg" alt="UCLA ECE Faculty pamarti"></div><div><img class="fac" src="/img/razavi_behzad.jpg" alt="UCLA ECE Faculty razavi_behzad"></div><div><img class="fac" src="/img/Samueli.jpg" alt="UCLA ECE Faculty samueli"></div><div><img class="fac" src="/img/ckkenyang.jpg" alt="UCLA ECE Faculty Yang Ken "></div></div><div class="page-caption faculty-caption"><b>Faculty:</b> Asad Abidi, Wentai Liu, Sudhakar Pamarti, Behzad Razavi, Henry Samueli, C.-K. Ken Yang. <b>Also</b>M.-C. Frank Chang, Y. Ethan Wang</div></div></div><div class="grid-layout"><div class="full"><h3 class="page-title">Sensor Information Acquisition, Processing and Applications</h3><p></p><div class="page-text"><p style="text-indent:0"><span class="first-letter">Sensory information</span> is foundational to modern electronic computing systems across a myriad of application domains such as health, energy, environment, and communications. Our research is developing innovative technologies for the entire waveform-to-decision pathway through which sensor information flows, often in real-time, distributed and resource-constrained settings. In work, we are developing (i) high-sensitivity and low-power transducers and A/D converters for capturing and digitizing sensor signals; (ii) high-performance hardware-software platforms for processing sensor data; and (iii) efficient algorithms and protocols for processing sensor data to derive rich inferences under power, processing, and security constraints in networked settings. Moreover, the results of our research are being applied to real systems in various applications.</p></div></div><div class="full"><div class="prof-pics"><div><img class="fac" src="/img/Corey-W-Arnold.jpg" alt="UCLA ECE Faculty Corey W. Arnold"></div><div><img class="fac" src="/img/candler2.jpg" alt="UCLA ECE Faculty Candler"></div><div><img class="fac" src="/img/emaminejad6.jpg" alt="UCLA ECE Faculty emaminejad"></div><div><img class="fac" src="/img/ozcan_nai3.jpg" alt="UCLA ECE Faculty ozcan"></div><div><img class="fac" src="/img/Asad-Madni2.jpg" alt="UCLA ECE Faculty Madni"></div><div><img class="fac" src="/img/greg-pottie.jpg" alt="UCLA ECE Faculty Greg Pottie"></div><div><img class="fac" src="/img/Mani-Srivastava.jpg" alt="UCLA ECE Faculty sirivastava"></div></div><div class="page-caption faculty-caption"><b>Faculty:</b> Corey W. Arnold, Robert Candler, Sam Emaminejad, Aydogan Ozcan, Asad M. Madni, Gregory Pottie, Mani Srivastava (pictured). Also: Omid Abari, Yang Zhang</div></div></div><div class="grid-layout"><div class="full"><h3 class="page-title">Embedded and Mobile Computing and Cyber-Physical Systems</h3><p></p><div class="page-text"><p style="text-indent:0"><span class="first-letter">We focus</span> on foundational hardware and software technologies and architectures for computing and communication capabilities necessary for emerging embedded, mobile, and cyber-physical systems. Such systems are found in emerging application domains of critical socio-economic importance, such as robotics, mobile health, sustainable built environments, smart electrical grids, smart water networks, and transportation systems. Our research addresses the fundamental capabilities needed by these systems such as energy efficiency, real-time performance, location awareness, precise time synchronization, adaptation to variations, secure operations, etc., and also develops novel implementation methods spanning the entire system stack from application and operating system software down to the processor, datapath, memory, and I/O hardware.</p></div></div><div class="full"><div class="prof-pics"><div><img class="fac" src="/img/Omid-Abari.jpg" alt="UCLA ECE Faculty Omid Abari"></div><div><img class="fac" src="/img/Anthony-Chen.jpg" alt="UCLA ECE Faculty Anthony Chen"></div><div><img class="fac" src="/img/Xiaofan-Cui-portrait.jpg" alt="UCLA ECE Faculty Xiaofan Cui"></div><div><img class="fac" src="/img/Lei.jpg" alt="UCLA ECE Faculty Lei He"></div><div><img class="fac" src="/img/Sarrafzadeh.jpg" alt="UCLA ECE Faculty Sarrafzadeh"></div><div><img class="fac" src="/img/Nader-Sehatbakhsh.jpg" alt="UCLA ECE Faculty Nader Sehatbakhsh"></div></div><div class="page-caption faculty-caption"><b>Faculty:</b> Omid Abari, Xiang Anthony Chen, Xiaofan Cui, Lei He, Majid Sarrafzadeh, Nader Sehatbakhsh, Yang Zhang (pictured). <b>Also:</b>Suhas Diggavi, Puneet Gupta, Ankur Mehta, Mani Srivastava, Paulo Tabuada</div></div></div><div class="grid-layout"><div class="full"><h3 class="page-title">Circuits and Embedded Systems Adjunct Faculty</h3><p></p></div><div class="full"><div class="prof-pics"><div><img class="fac" src="/img/ShervinMoloudi.jpg" alt="UCLA ECE Faculty Shervin Moloudi"></div></div><div class="page-caption faculty-caption"><b>Adjunct Faculty:</b> Shervin Moloudi</div></div></div>      '
