    textCopy: '   <div class="grid-layout"><div class="full"><h3 class="page-title">Electromagnetics</h3><p></p><p style="text-indent:0"><span class="first-letter">Electromagnetics</span>embodies all aspects of science and engineering topics stemming from Maxwells equations, describing the behavior of electric and magnetic fields and their interactions with electric charges and currents. The science of electromagnetics underlies nearly all modern electric, computation, and communications technology. Both our coursework and research address theoretical, computational, optimization, design and measurement aspects of electromagnetic devices for a variety of applications, including wireless communications, satellite, space and ground systems, medical and sensor applications, multi-function antennas and metamaterials in frequencies ranging from microwaves and millimeter waves to terahertz.</p></div><div class="full"><div class="prof-pics"><div><img class="fac" src="/img/Aydin-Babakhani.jpg" alt="UCLA ECE Faculty Aydin Babakhani"></div><div><img class="fac" src="/img/SergioCarbajo.jpg" alt="UCLA ECE Faculty Sergio Carbajo"></div><div><img class="fac" src="/img/Frank_Chang.jpg" alt="UCLA ECE Faculty CESM.-C. Frank Chang"></div><div><img class="fac" src="/img/Liou_Kuo-Nan.jpg" alt="UCLA ECE Faculty Kuo-Nan Liou"></div><div><img class="fac" src="/img/rahmat-samii04.jpg" alt="UCLA ECE Faculty Yahya Rahmat-Samii"></div><div><img class="fac" src="/img/ethanwang.jpg" alt="UCLA ECE Faculty Ethan Wang"></div></div><div class="page-caption faculty-caption"><b>Faculty:</b>Aydin Babakhani, Sergio Carbajo, M.-C. Frank Chang, Kuo-Nan Liou, Yahya Rahmat-Samii, Y. Ethan Wang (pictured). Also Robert Candler, Mona Jarrahi, Warren Mori, Aydogan Ozcan, Benjamin Williams, Chee Wei Wong</div><div class="spacer"><img src="/img/pixel.png"></div></div></div><div class="grid-layout"><div class="full"><h3 class="page-title">Nanoelectronics, Devices and Heterogeneous Integration</h3><p></p><div class="page-text"><p style="text-indent:0"><span class="first-letter">In the area</span> of nanoelectronics and solid-state devices, UCLA is at the forefront of research. Major research efforts are ongoing for the synthesis and design of advanced materials, such as two-dimensional semiconductors, topological insulators, and magnetic oxides  both their fundamental properties and their applications to ultra-scaled logic and memory devices. We also have extensive activities in the design and fabrication of ultra-scaled devices on both silicon and compound semiconductors, such as power and RF transistors, spin-based switches and memory elements, tunnel FETs, and other novel devices that are suitable for applications that go beyond conventional scaling. Another major thrust is the development of neuromorphic (i.e., brain-inspired) devices and nanosystems for computation and information processing. Nanoelectronic devices are under investigation for use as sensors for compact biomedical sensing tools. This area also includes the development of Micro- and Nano-Electromechanical Systems (MEMS and NEMS). In addition, we investigate new methods of interconnecting heterogeneous devices for a more holistic interpretation of Moores law. Many of these research activities take place in collaboration with other research groups, both within and outside ECE, on circuit/system/device co-design and co-optimization. The nanoelectronics research in the department is supported by advanced commercial simulation tools for device analysis, a state-of-the-art nano-fabrication facility, and device characterization equipment with capability from DC to over 100GHz.</p></div></div><div class="full"><div class="prof-pics"><div><img class="fac" src="/img/Elaheh-Ahmadi.jpg" alt="UCLA ECE Faculty Elaheh Ahmadi"></div><div><img class="fac" src="/img/Clarice_D_Aiello.jpg" alt="UCLA ECE Faculty Clarice Aiello"></div><div><img class="fac" src="/img/candler2.jpg" alt="UCLA ECE Faculty Rob Candler"></div><div><img class="fac" src="/img/SergioCarbajo.jpg" alt="UCLA ECE Faculty Sergio Carbajo"></div><div><img class="fac" src="/img/emaminejad6.jpg" alt="UCLA ECE Faculty Sam Emaminejad"></div><div><img class="fac" src="/img/Mona-Jarrahi.jpg" alt="UCLA ECE Faculty Mona Jarrahi"></div><div><img class="fac" src="/img/DwightStreit.jpg" alt="UCLA ECE Faculty Dweit Streit"></div><div><img class="fac" src="/img/wang.jpg" alt="UCLA ECE Faculty Kang Wang"></div><div><img class="fac" src="/img/CheeWeiWong3.jpg" alt="UCLA ECE Faculty Chee Wei Wong"></div><div><img class="fac" src="/img/Woo.jpg" alt="UCLA ECE Faculty Jason Woo"></div></div><div class="page-caption faculty-caption"><b>Faculty:</b> Elaheh Ahmadi, Clarice Aiello, Robert Candler, Sergio Carbajo, Sam Emaminejad, Mona Jarrahi, Dwight Streit, Kang Wang, Chee Wei Wong, Jason C. S. Woo (pictured). Also Aydin Babakhani, Chi On Chui, Subramanian S. Iyer.</div><div class="spacer"><img src="/img/pixel.png"></div><div class="prof-pics"><div><img class="fac" src="/img/Fred_G_Allen.jpg" alt="UCLA ECE Faculty Fred Allen"></div><div><img class="fac" src="/img/fetterman.jpg" alt="UCLA ECE Faculty Harrold Fetterman"></div><div><img class="fac" src="/img/Dee_Son_Pan.jpg" alt="UCLA ECE Faculty dee son pan"></div><div><img class="fac" src="/img/king-ning-tu.png" alt="UCLA ECE Faculty king ning tu"></div></div><div class="page-caption faculty-caption"><b>Emeriti Faculty:</b> Fred G. Allen, Harold Fetterman, Dee Son Pan, King-Ning Tu</div><div class="spacer"><img src="/img/pixel.png"></div></div></div><div class="grid-layout"><div class="full"><h3 class="page-title">Photonics</h3><p></p><div class="page-text"><p style="text-indent:0"><span class="first-letter">Photonics</span> deals with the generation, detection, and manipulation of light  specifically how it can be harnessed to provide useful functions. For example, nearly all of the information on the internet is transported by encoding it onto signals of infrared light carried on optical fibers. Many unknown materials can be identified by studying how light interacts with them (i.e., by scattering, absorbing, phase shifting, or polarizing some incident photons). The name photonics emphasizes the importance of quantum mechanical properties of light and its interaction with matter. Many topics in photonics research involve development and/or use of lasers. Our research program encompasses topics such as how light interacts both with matter on the nanoscale and in plasmas, as well as new ways to generate, detect, and control light in underdeveloped spectral regions. Applications under investigation include the diverse topics of solar energy generation, lensless microscopy for cell-phone based telemedicine, high energy laser wakefield particle accelerators, ultra-high frame rate imaging, silicon photonics, biomedical imaging, and use of stabilized lasers for ultra-high precision measurements  just to name a few.</p></div></div><div class="full"><div class="prof-pics"><div><img class="fac" src="/img/Arisaka_Katsushi.jpg" alt="UCLA ECE Faculty  Arisaka_Katsushi"></div><div><img class="fac" src="/img/SergioCarbajo.jpg" alt="UCLA ECE Faculty  Sergio Carbajo"></div><div><img class="fac" src="/img/Jia-Ming-Liu.jpg" alt="UCLA ECE Faculty  Jia-Ming-Liu"></div><div><img class="fac" src="/img/Ozcan_Aydogan.jpg" alt="UCLA ECE Faculty Ozcan"></div><div><img class="fac" src="/img/williams.jpg" alt="UCLA ECE Faculty  Ben Williams"></div><div><img class="fac" src="/img/fetterman.jpg" alt="UCLA ECE Faculty Harold Fetterman"></div><div><img class="fac" src="/img/PATEL-emeritus.jpg" alt="UCLA ECE Faculty Patel"></div><div><img class="fac" src="/img/stafsudd.jpg" alt="UCLA ECE Faculty  Stafsudd"></div></div><div class="page-caption faculty-caption"><b>Faculty:</b>Katsushi Arisaka, Sergio Carbajo, Jia-Ming Liu, Aydogan Ozcan, Benjamin Williams (pictured). Also Clarice Aiello, Mona Jarrahi, Chan Joshi, Chee Wei Wong.<b>Emeriti Faculty:</b>Harold Fetterman, C. Kumar Patel, Oscar Stafsudd</div></div></div><div class="grid-layout"><div class="full"><h3 class="page-title">Plasma Electronics</h3><p></p><div class="page-text"><p style="text-indent:0"><span class="first-letter">Plasma  Electronics</span> covers a wide spectrum of activities that include electro-dynamics of charged particles in external fields, non-linear optics of plasmas, high energy-density plasmas, laser-plasma interactions, basic plasma behavior, computer simulations of laboratory and space plasmas and fusion plasmas. The applications being studied are plasma-based charged particle accelerators, free electron lasers, other plasma-based radiation sources, laser-fusion, astrophysical plasmas, plasma propulsion, gas lasers and plasmas for lighting. There are opportunities to do experimental, theoretical and computer simulations research in all these areas. Close collaborations exist with Livermore National Laboratory and SLAC and UCLAs state-of-the art laboratories including the NeptuneLaboratory that houses the worlds most powerful CO2 laser. The research group also has the Dawson II computational cluster for research on inertial confinement fusion, plasma accelerators and astrophysical plasmas.</p></div></div><div class="full"><div class="prof-pics"><div><img class="fac" src="/img/Joshi.jpg" alt="UCLA ECE Faculty  Joshi"></div><div><img class="fac" src="/img/WarrenMori.jpg" alt="UCLA ECE Faculty Warren Mori"></div><div><img class="fac" src="/img/Francis_Chen.jpg" alt="UCLA ECE Faculty Chen"></div></div><div class="page-caption faculty-caption"><b>Faculty:</b>Chan Joshi, Warren Mori.<b>Emeritus Faculty:</b>Francis F. Chen</div><div class="spacer"><img src="/img/pixel.png"></div></div></div><div class="grid-layout"><div class="full"><h3 class="page-title">Physical &Wave Electronic Adjunct Faculty</h3><div class="prof-pics"><div><img class="fac" src="/img/Chui.jpg" alt="UCLA ECE Faculty Chi On Chui"></div><div><img class="fac" src="/img/Goebel.jpg" alt="UCLA ECE Faculty Dan Goebel"></div><div><img class="fac" src="/img/Asad-Madni2.jpg" alt="UCLA ECE Faculty Asad Madni"></div><div><img class="fac" src="/img/Yair_Rivenson.jpg" alt="UCLA ECE Faculty Yair Rivenson "></div><div><img class="fac" src="/img/Taylor.jpg" alt="UCLA ECE Faculty Zachary Taylor"></div><div><img class="fac" src="/img/Eli-Yablonovitch.jpg" alt="UCLA ECE Faculty Eli Yablonovitch"></div></div><div class="page-caption faculty-caption"><b>Adjunct Faculty:</b>Chi On Chui, Dan Goebel, Asad M. Madni, Yair Rivenson, Zachary Taylor, Eli Yablonovitch</div><div class="spacer"><img src="/img/pixel.png"></div></div></div>'
