    textCopy: '<div class="grid-layout "> <div class="full"> <h3 class="page-title">Communications and Networking</h3> <p></p><div class="page-text"> <p style="text-indent:0"><span class="first-letter">UCLA has </span> a tradition in Communications and Networking research. For example, the first Internet packets were sent from UCLA and the Viterbi Algorithm was developed here. In an increasingly interconnected and online world, our research encompasses computer networks, social networks, wireless networks, on-chip networks and biological networks. Our work spans from fundamental questions in communications networking, autonomous vehicular networks, multimedia telecommunications, coding theory, algorithms, resource allocation, game theory, network economics, information theory and security to applications in mobile computing, sensors and embedded systems, distributed control systems, media distribution, green computing, intelligent cities, smart grid, cognitive radios, emergency networks and mobile health.</p></div></div><div class="full"> <div class="prof-pics"> <div><img class="fac" src="/img/Omid-Abari.jpg" alt="UCLA ECE Faculty Omid Abari"></div><div><img class="fac" src="/img/Fragouli-Teaching.jpg" alt="UCLA ECE Faculty Christina Fragouli"></div><div><img class="fac" src="/img/AliMosleh.jpg" alt="UCLA ECE Faculty Ali Mosleh"></div><div><img class="fac" src="/img/pottie.jpg" alt="UCLA ECE Faculty Gregory Pottie"></div><div><img class="fac" src="/img/ian-roberts-portrait.jpg" alt="UCLA ECE Faculty Ian Roberts"></div><div><img class="fac" src="/img/zhang.jpg" alt="UCLA ECE Faculty Lixia Zhang"></div></div><div class="page-caption faculty-caption"><b>Faculty:</b> Omid Abari, Christina Fragouli, Ali Mosleh, Gregory Pottie, Ian Roberts, Lixia Zhang (pictured). Also Danijela Cabric, Suhas Diggavi, Asad M. Madni, Ankur Mehta, Mihaela van der Schaar, Mani Srivastava, Paulo Tabuada, John Villasenor, Richard D. Wesel. </div><div class="prof-pics"> <div><img class="fac" src="/img/Speyer.jpg" alt="UCLA ECE Faculty Jaon Speyer"></div><div><img class="fac" src="/img/Kung-Yao.jpg" alt="UCLA ECE Faculty Kung-Yao"></div></div><div class="page-caption faculty-caption"><b>Emeriti Faculty: </b>Jason L. Speyer, Kung Yao (pictured)</div><div class="spacer"><img src="/img/pixel.png"></div></div></div></div><div class="grid-layout "> <div class="full"> <h3 class="page-title">Signal Processing and Machine Learning</h3> <p></p><div class="page-text"> <p style="text-indent:0"><span class="first-letter">We rely </span> on signals to interact with the physical and virtual world. A challenge today is how to collect, analyze, store, and process large data in an efficient and scalable manner. Our signal processing research targets the inference, visualization,representation, and learning of a broad spectrum of signals related to media (including speech, video and social media), sensors (for medical, military, space, process control or environmental applications), communications and control networks (wireless and utility networks), and adaptive arrays (such as acoustic and radar). This applies to problems ranging in scale from the microscopic to Big Data. Research contributes to disciplines descended from both Electrical Engineering and Computer Sciences roots, such as machine learning, statistical signal processing, stochastic modeling, graphical models, information theory, adaptation and learning algorithms, inference over networks, distributed signal processing, data analysis and distributed optimization.</p></div></div><div class="full"> <div class="prof-pics"> <div><img class="fac" src="/img/AbeerAlwan-profile.jpg" alt="UCLA ECE Faculty AlwanAbeer"></div><div><img class="fac" src="/img/Corey-W-Arnold.jpg" alt="UCLA ECE Faculty Corey W. Arnold "></div><div><img class="fac" src="/img/Cabric.jpg" alt="UCLA ECE Faculty SS Danijela Cabric"></div><div><img class="fac" src="/img/Dolecek_Lara.jpg" alt="UCLA ECE Faculty Dolecek Lara"></div><div><img class="fac" src="/img/Achuta-Kadambi.jpg" alt="UCLA ECE Faculty kadabi"></div><div><img class="fac" src="/img/JonathanKao.jpg" alt="UCLA ECE Faculty Jonathan Kao"></div><div><img class="fac" src="/img/mayank-mehta.jpg" alt="UCLA ECE Faculty Mayank Mehta"></div><div><img class="fac" src="/img/Soatto.jpg" alt="UCLA ECE Faculty Soatto"></div><div><img class="fac" src="/img/Fabien_Scalzo.jpg" alt="UCLA ECE Faculty Fabien Scalzo"></div><div><img class="fac" src="/img/Villasenor.jpg" alt="UCLA ECE Faculty Villasenor"></div></div><div class="page-caption faculty-caption"><b>Faculty:</b> Abeer Alwan, Corey W. Arnold, Danijela Cabric, Lara Dolecek, Achuta Kadambi, Jonathan Kao, Mayank Mehta, Stefano Soatto, Fabien Scalzo, John Villasenor (pictured). Also Omid Abari, Alyson Fletcher, Lei He, Suhas Diggavi, Asad M. Madni, Gregory Pottie, Ian Roberts, Vwani Roychowdhury, Mihaela van der Schaar, Mani Srivastava, Paulo Tabuada, Lieven Vandenberghe, Lin F. Yang.</div><div class="prof-pics"> <div><img class="fac" src="/img/Rajeev_Jain.jpg" alt="UCLA ECE Faculty Rajeev_Jain"></div><div><img class="fac" src="/img/Alan-Willson.jpg" alt="UCLA ECE Faculty Alan-Willson"></div><div><img class="fac" src="/img/Kung-Yao.jpg" alt="UCLA ECE Faculty Kung-Yao"></div></div><div class="page-caption faculty-caption"><b>Emeriti Faculty: </b>Rajeev Jain, Alan Willson, Kung Yao</div><div class="spacer"><img src="/img/pixel.png"></div></div></div><div class="grid-layout "> <div class="full"> <h3 class="page-title">Control and Decision Systems</h3> <p></p><div class="page-text"> <p style="text-indent:0"><span class="first-letter">Control and decision </span> systems research aims to develop the mathematical principles explaining how complex systems can behave correctly in uncertain environments. One key program strength is in cyber-physical systems that network together collaborating computational elements with physical elements. We work in improving their functionality, autonomy, and adaptability, analyze their performance and ensure their secure operation. Applications include autonomous vehicles, transportation networks, medical systems, robotics coordination, smart buildings, smart power grids, and smart manufacturing. Another strength is in the intersection of economics with control engineering.</p></div></div><div class="full"> <div class="prof-pics"> <div><img class="fac" src="/img/Panagiotis.jpg" alt="UCLA ECE Faculty Panagiotis"></div><div><img class="fac" src="/img/bahmangharesifard.jpg" alt="UCLA ECE Faculty Bahman Gharesifard"></div><div><img class="fac" src="/img/Mihaela.jpg" alt="UCLA ECE Faculty Mihaela"></div><div><img class="fac" src="/img/Speyer.jpg" alt="UCLA ECE Faculty speyer"></div><div><img class="fac" src="/img/Tabuada-Headshot.jpg" alt="UCLA ECE Faculty Tabuada"></div><div><img class="fac" src="/img/LinFYang.jpg" alt="UCLA ECE Faculty Lin Yang"></div></div><div class="page-caption faculty-caption"><b>Faculty:</b> Panagiotis Christofides, Bahman Gharesifard, Mihaela van der Schaar, Jason L. Speyer, Paulo Tabuada, Lin F. Yang (pictured) Also Suhas Diggavi, Achuta Kadambi, Ankur Mehta, Ali Mosleh, Lieven Vandenberghe. </div><div class="prof-pics"> <div><img class="fac" src="/img/Laub.jpg" alt="UCLA ECE Faculty Laub"></div></div><div class="page-caption faculty-caption"><b>Emeritus Faculty:</b> Alan Laub </div><div class="spacer"><img src="/img/pixel.png"></div></div></div><div class="grid-layout "> <div class="full"> <h3 class="page-title">Information, Computation and Optimization Theory</h3> <p></p><div class="page-text"> <p style="text-indent:0"><span class="first-letter">Information theory </span> research develops the fundamental limits of compression, encryption, and channel coding of data in a variety of networks, storage media and communications systems. Application areas include new types of storage systems and more efficient and secure networks. Computation theory research characterizes the fundamental complexity of problems and the types of algorithms that can be used to solve them efficiently and/or approximately. Optimization theory research studies how minimizing cost functions inherent in problems spanning from economics to broad swaths of engineering can be formulated and efficiently solved.</p></div></div><div class="full"> <div class="prof-pics"> <div><img class="fac" src="/img/Diggavi.jpg" alt="UCLA ECE Faculty Diggavi"></div><div><img class="fac" src="/img/Allie-Fletcher.jpg" alt="UCLA ECE Faculty Fletcher"></div><div><img class="fac" src="/img/StanleyOsher.jpg" alt="UCLA ECE Faculty Stanley Osher"></div><div><img class="fac" src="/img/Roychouwdhury.jpg" alt="UCLA ECE Faculty Roychouwdhury"></div><div><img class="fac" src="/img/vandenberghe.jpg" alt="UCLA ECE Faculty leiven"></div><div><img class="fac" src="/img/Richard_Wesel.jpg" alt="UCLA ECE Faculty Richard Wesel"></div></div><div class="page-caption faculty-caption"><b>Faculty: </b> Suhas Diggavi, Alyson Fletcher, Stanley Osher, Vwani Roychowdhury, Lieven Vandenberghe, Richard D. Wesel (pictured). Also Lara Dolecek, Christina Fragouli, Bahman Gharesifard, Ankur Mehta, Mihaela van der Schaar, Paulo Tabuada, Lin F. Yang. <b>Emeritus Faculty:</b> Stephen Jacobsen</div><div class="spacer"><img src="/img/pixel.png"></div></div></div><div class="grid-layout "> <div class="full"> <h3 class="page-title">Signals &Systems Adjunct Faculty</h3> <div class="page-text"></div></div><div class="full"> <div class="prof-pics"> <div><img class="fac" src="/img/DariushDivsalar.jpg" alt="UCLA ECE Faculty Divsalar"></div><div><img class="fac" src="/img/Asad-Madni-2016.jpg" alt="UCLA ECE Faculty Madni"></div></div><div class="page-caption faculty-caption"><b>Adjunct Faculty: </b> Dariush Divsalar, Asad M. Madni</div><div class="spacer"><img src="/img/pixel.png"></div></div></div>  '
