    textCopy: ' <div class="grid-cards"> <h3 class="page-title">American Physical Society </h3> <div class="page-text"> <p style="text-indent:0">Professor Mona Jarrahi has been named a fellow of the American Physical Society. The fellowship program recognizes researchers who have made significant contributions in the application of physics to science and technology, as well as advances in physics through original publication.</p><p> The nonprofit organization recently announced its 2022 fellows and selected Jarrahi for her pioneering research, development, and commercialization of plasmonic terahertz optoelectronics, enabling high-sensitivity, high-throughput terahertz sensing, and imaging systems.</p><p> Jarrahi holds the Northrop Grumman Endowed Chair in Electrical and Computer Engineering and has received many awards, including the 2021 Institution of Engineering and Technology (IET) A F Harvey Engineering Research Prize and the 2016 Moore Inventor Fellowship. She is also a fellow of Optical Society (OPTICA), Institute of Electrical and Electronics Engineers (IEEE), Institute of Physics (IOP), and the International Society for Optical Engineering (SPIE).</p><p> A member of the California NanoSystems Institute at UCLA, professor Mona Jarrahi leads theTerahertz Electronics Laboratory, which focuses on new materials and device concepts to improve the performance of terahertz and millimeter-wave devices.</p></div><div class="card-deck"> <div class="card"> <div class="frame"><img class="card-pic" src="/img/Mona-Jarrahi.jpg" alt="UCLA ECE Faculty Mona Jaharri"></div><div class="prof-title">Professor</div><div class="prof-name">Mona Jaharri</div><ul class="card-bottom"> <li>American Physical Society (APS), 2022</li><li>Institution of Engineering and Technology (IET) A F Harvey Engineering Research Prize, 2021.</li><li>Institute of Physics (IOP), 2020</li><li>Institute of Electrical and Electronics Engineers (IEEE), 2019</li><li>International Society for Optical Engineering (SPIE), 2018</li><li> </li><li>Research: <ul> <li>Terahertz/millimeter-wave electronics and optoelectronics</li><li>Microwave photonics</li><li>Imaging and spectroscopy systems</li></ul> </li><li> </li></ul> </div></div><div class="spacer"><img src="/img/pixel.png"></div><h3 class="page-title">National Academy of Engineering</h3> <div class="page-text"> <p style="text-indent:0">Founded in 1964, the <span> National Academy of Engineering </span> is a private, independent, nonprofit institution. It is part of the National Academies of Sciences, Engineering, and Medicine, with more than 2,000 peer-elected members and international members, comprising senior professionals in business, academia and government who are among the worlds most accomplished engineers. Professor Jalali was elected for contributions to silicon photonics, high time-resolution scientific instruments, and biomedical imaging.</p><p>38 affiliated Samueli School of Engineering faculty are members of the National Academy of Engineering.</p></div><div class="card-deck"> <div class="card"> <div class="frame"> <img class="card-pic" src="/img/Jalali_NAE.png" alt="UCLA ECE Faculty B Jalali"> </div><div class="prof-title">Distinguished Professor Emeritus</div><div class="prof-name"> Bahram Jalali</div><ul class="card-bottom"> <li>NAE Fellow, 2022.</li><li>National Academy of Inventors, 2020. </li><li>Society of Photo-Optical Instrumentation Engineers, 2013.</li><li>American Physical Society, 2011.</li><li>Optical Society of America, 2004.</li><li>IEEE Fellow, 2003.</li><li> </li></ul> </div></div><div class="spacer"><img src="/img/pixel.png"></div><h3 class="page-title">American Institute for Medical and Biological Engineering</h3> <div class="page-text"> <p style="text-indent:0"> The <span>American Institute for Medical and Biological Engineering (AIMBE) </span> has announced the election of Dejan Markovic to its College of Fellows. Dr. Markovic was nominated, reviewed, and elected by peers and members of the AIMBE College of Fellows for outstanding contributions to neurotechnology for clinical and translational neuroscience.</p><p> The College of Fellows is comprised of the top two percent of medical and biological engineers in the country  the most accomplished and distinguished engineering and medical school chairs, research directors, professors, innovators, and successful entrepreneurs. <span>www.aimbe.org </span></p></div><div class="card-deck"> <div class="card"> <div class="frame"><img class="card-pic " src="/img/markovic5.jpg" alt="UCLA ECE Faculty Dejan Markovic"></div><div class="prof-title"> Professor</div><div class="prof-name">Dejan Markovic</div><ul class="card-bottom"> <li>AIMBE, 2022.</li><li>IEEE Fellow, 2021.</li><li>NSF Career Award, 2009.</li><li></li></ul> </div></div><div class="spacer"><img src="/img/pixel.png"></div><div class="spacer"><img src="/img/pixel.png"></div><h3 class="page-title">IEEE</h3> <div class="page-text"> <p style="text-indent:0"> The <span>Institute of Electrical and Electronics Engineers </span> has announced the election of Puneet Gupta a Fellow of the Class of 2022. The IEEE is the world\'s largest technical professional organization dedicated to advancing technology for the benefit of humanity. It is designed to serve professionals involved in all aspects of the electrical, electronic, and computing fields and related areas of science and technology that underlie modern civilization.</p><p> IEEE\'s roots go back to 1884 when electricity began to become a major influence in society. There was one major established electrical industry, the telegraph, which since the 1840s had come to connect the world with a data communications system faster than the speed of transportation. Names such as Thomas Edison, Alexander Graham Bell and founding President Norvin Green of Western Union, saw technologies evolve from the telegraph, telephone, electric power, radio, and modern globalized communication systems of today. It has over 400,000 members in 160 countries. <span>www.ieee.org </span></p></div><div class="card-deck"> <div class="card"> <div class="frame"><img class="card-pic " src="/img/Puneet-Gupta.jpg" alt="UCLA ECE Faculty Punet Gupta"></div><div class="prof-title"> Professor</div><div class="prof-name">Puneet Gupta</div><ul class="card-bottom"> <li>IEEE Fellow, 2022.</li><li>IBM Faculty Award, 2012</li><li>ACM/SIGDA Outstanding New Faculty Award, 2010.<li></li><li>SRC Inventor Recognition Award, 2009.</li><li>NSF CAREER Award, 2009.</li><li></li></ul> </div></div><div class="spacer"><img src="/img/pixel.png"></div></div> '
